/**
 * TEST SIMPLE - VALIDATION CONFIGURATION JEST
 */

describe('Configuration Jest', () => {
  it('doit exécuter les tests correctement', () => {
    expect(1 + 1).toBe(2);
  });

  it('doit gérer les assertions basiques', () => {
    const testString = 'MyFitHero';
    expect(testString).toBe('MyFitHero');
    expect(testString.length).toBeGreaterThan(5);
  });

  it('doit gérer les objets et arrays', () => {
    const testObject = { name: 'test', value: 42 };
    expect(testObject).toHaveProperty('name');
    expect(testObject.value).toBe(42);

    const testArray = [1, 2, 3, 4, 5];
    expect(testArray).toHaveLength(5);
    expect(testArray).toContain(3);
  });
});
